.fp-watermark {
    display: none !important;
    position: absolute !important;
    left: -9999px !important;
    z-index: -99999999;
}

// typography
$ff-1: 'Manrope', Arial, sans-serif;
$ff-2: 'Coolvetica Rg', Arial, sans-serif;

// pallets
$color-black-1: #000;
$color-black-2: #485447;
$color-white-1: #F8F5F0;
$color-white-2: #ECEDDF;
$color-white-3: #B3BFA6;
$color-gray-1: #808D81;
$color-gray-2: #D2D3BB;
$color-gray-3: #6A7267;
$color-accent-1: #2C392D;
$color-accent-2: #334435;
$color-accent-3: #3C4E3E;
$color-red-1: #D43B31;
$color-yellow-1: #D7863B;
$color-green-1: #4E8F52;

// media breakpoints
$xll: 1899px;
$xll-mf: $xll + 1;

$xl: 1420px;
$xl-mf: $xl + 1;

$lg: 1199px;
$lg-mf: $lg + 1;

$md: 991px;
$md-mf: $md + 1;

$sm: 767px;
$sm-mf: $sm + 1;

$xs: 576px;
$xs-mf: $xs;