.l-account-main {
    overflow: hidden;
    &__container {
    }
    &__row {
        flex-wrap: nowrap;
        @media (max-width: $lg) {
            flex-wrap: wrap;
        }
    }
    &__col {
        --account-main-p-t: 16rem;
        --account-main-p-b: 5rem;
        @media (max-width: $lg) {
            max-width: 100%;
            flex-basis: 100%;
        }
        &-aside {
            min-height: 100vh;
            padding-bottom: var(--account-main-p-b);
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            position: relative;
            @media (max-width: $lg) {
                min-height: 0;
                padding-bottom: 0;
            }
            &:before {
                background: $color-white-2;
                width: 100vw;
                content: '';
                position: absolute;
                top: 0;
                right: var(--gap);
                bottom: 0;
                z-index: -1;
                @media (max-width: $lg) {
                    display: none;
                }
            }
        }
        &-main {
            max-width: 53.6rem;
            margin-left: 3.1rem;
            padding-top: var(--account-main-p-t);
            padding-bottom: var(--account-main-p-b);
            flex: 0 0 53.6rem;
            @media (max-width: $lg) {
                max-width: 100%;
                margin-left: 0;
                padding-top: 5.6rem;
                padding-bottom: 4rem;
                flex: 0 0 100%;
            }
        }
        &-user {
            max-width: 58.1rem;
            margin: 0 var(--gap-fluid) 0 8rem;
            padding-top: var(--account-main-p-t);
            padding-bottom: var(--account-main-p-b);
            flex: 0 0 58.1rem;
        }
    }
    &__hero {
        position: relative;
        &-center {
            width: 83.6%;
            max-width: 83.6rem;
            margin: 0 auto;
            user-select: none;
            pointer-events: none;
            @media (max-width: $xll) {
                left: calc(var(--gap-fluid) / -2);
            }
            @media (max-width: $lg) {
                width: 62.6%;
                left: 0;
            }
        }
        &-coin {
            background: url(../images/icon/coin.svg) 50% no-repeat;
            background-size: contain;
            width: 5.2rem;
            height: 5.2rem;
            position: absolute;
            top: 50%;
            left: 50%;
        }
    }
    &__picture {
        user-select: none;
        pointer-events: none;
        img {
            width: 100%;
            height: auto;
            max-height: calc(100vh - 6rem);
            display: block;
            object-fit: contain;
            object-position: right top;
            @media (max-width: $xll) {
                margin-right: auto;
            }
            @media (max-width: $lg) {
                max-height: 99rem;
            }
        }
    }
    &__nav-wrap {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 0;
        left: 0;
    }
    &__nav {
        max-width: 100%;
        margin: 0 0 10rem;
        font-family: $ff-2;
        ul {
            margin: 0;
            padding: 0;
            list-style-type: none;
        }
        li {
            margin: 0;
            padding: 0;
            & + & {
                margin-top: 3.6rem;
            }
        }
        a {
            font-size: var(--fs-3);
            display: block;
            transition: all .34s;
            &:hover {
                text-decoration: none;
                @media (min-width: $lg-mf) {
                    opacity: 1 !important;
                }
            }
            &:not(.is-active) {
                opacity: .5;
            }
        }
    }
    &__aside-btn {
        width: 100%;
        @media (max-width: $xll) {
            width: calc(100% - var(--gap-fluid));
            margin-top: 5.6rem;
        }
    }
    &__content {
        @media (max-width: $lg) {
            display: flex;
            flex-direction: column;
        }
    }
    &__title {
        margin: 0 0 2.4rem;
        font-size: var(--fs-8);
        text-transform: uppercase;
        @media (max-width: $md) {
            margin-bottom: 1.6rem;
            font-size: 2.8rem;
        }
    }
    &__text {
        margin: 0 0 5.6rem;
        font-weight: 500;
        font-size: var(--fs-6);
        color: rgba($color-accent-1, .7);
        @media (max-width: $md) {
            margin-bottom: 3.6rem;
            font-size: 1.6rem;
        }
        a {
            color: $color-accent-1;
            transition: all .34s;
            &:hover {
                text-decoration: none;
                @media (min-width: $lg-mf) {
                    color: $color-gray-3;
                }
            }
        }
    }
    &__form {
    }
    &__code-picture {
        width: 100%;
        max-width: 11.8rem;
        margin: 0 auto 5.6rem;
        @media (max-width: $lg) {
            max-width: 12.4rem;
            margin-top: 7rem;
            order: -1;
        }
        img {
            width: 100%;
            height: auto;
            display: block;
        }
    }
    &__course-list {
        max-height: 100vh;
        margin-right: calc(-2 * var(--account-main-p-b));
        margin-top: calc(-1 * var(--account-main-p-t));
        margin-bottom: calc(-1 * var(--account-main-p-b));
        padding-top: var(--account-main-p-t);
        padding-right: calc(2 * var(--account-main-p-b));
        position: relative;
        overflow-x: hidden;
        overflow-y: auto;
        @media (max-width: $xll) {
            margin-right: calc(-2 * var(--account-main-p-b) - 11rem);
            padding-right: calc(2 * var(--account-main-p-b) + 11rem);
        }
        @media (max-width: $lg) {
            max-height: 9999rem;
            margin: 0;
            padding: 0;
            overflow: hidden;
        }
        &:after {
            background: linear-gradient(180deg, rgba($color-white-1, 0) 0%, $color-white-1 100%);
            width: 100%;
            height: 10rem;
            margin-top: -2rem;
            content: '';
            display: block;
            position: sticky;
            bottom: 0;
            left: 0;
            pointer-events: none;
            @media (max-width: $lg) {
                display: none;
            }
        }
        &-card {
            & + & {
                margin-top: 1rem;
            }
        }
    }
    &__course-empty {
        @media (max-width: $lg) {
            padding-top: 13rem;
        }
        &-title {
            margin: 0 0 2.4rem;
            text-align: center;
            @media (max-width: $md) {
                font-size: 2.4rem;
            }
        }
        &-text {
            margin: 0 0 5.6rem;
            font-weight: 500;
            font-size: var(--fs-6);
            color: rgba($color-accent-1, .7);
            text-align: center;
            @media (max-width: $md) {
                margin-bottom: 2.6rem;
                font-size: 1.6rem;
            }
        }
        &-picture {
            max-width: 46.8rem;
            margin: 0 auto 5.6rem;
            pointer-events: none;
            user-select: none;
            img {
                width: 100%;
                height: auto;
                display: block;
            }
        }
        &-btn {
            @media (max-width: $md) {
                --btn-padding-y: 1.4rem;
                min-height: 4.8rem;
            }
        }
    }
    &__footer-btn {
        margin-top: 5.6rem;
    }
}

.page-account--theme-dark {
    .l-account-main__col-aside {
        padding-top: var(--account-main-p-t);
        color: $color-white-1;
        &:before {
            background: $color-accent-1;
        }
        picture {
            img {
                @media (max-width: $xll) {
                    max-height: calc(100vh - 17rem);
                }
            }
        }
    }
    .l-account-main__hero-center {
        max-width: 78.5rem;
        margin-top: calc(-1 * var(--account-main-p-t));
        margin-right: 9rem;
        margin-left: auto;
        position: relative;
        z-index: -1;
        @media (max-width: $xll) {
            margin-right: auto;
        }
    }
    .l-account-main__col-user {
        @media (max-width: $xll) {
            max-width: 40.5%;
            margin-right: 11rem;
            margin-left: 14rem;
            flex-basis: 40.5%;
        }
        @media (max-width: $xl) {
            margin-right: 4rem;
            margin-left: 7rem;
        }
        @media (max-width: $lg) {
            max-width: 100%;
            margin: 0;
            flex-basis: 100%;
        }
        @media (max-width: $md) {
            padding-top: 11.4rem;
        }
    }
}