.l-form-account {
    &__row {
    }
    &__col-line {
        &.row__col--6 {
            @media (max-width: $sm) {
                max-width: 100%;
                flex-basis: 100%;
            }
        }
        &.row__col--12 {
        }
        &:not(:last-child) {
            margin-bottom: 4rem;
            @media (max-width: $md) {
                margin-bottom: 2.4rem;
            }
        }
        &:last-child {
            @media (max-width: $md) {
                margin-top: 2.8rem;
            }
        }
    }
    &__form {
    }
    &__btn {
        @media (max-width: $md) {
            --btn-padding-y: 1.4rem;
            min-height: 4.8rem;
        }
    }
    &__btn-moore {
        @media (max-width: $md) {
            --btn-padding-y: 1.4rem;
            min-height: 4.8rem;
        }
        & + & {
            margin-top: 1.2rem;
        }
    }
    &__footer {
        padding-top: .6rem;
        text-align: center;
        @media (max-width: $lg) {
            padding-top: .1rem;
            font-size: 1.2rem;
        }
    }
}