.header-account {
    width: 100%;
    padding: 2.4rem 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    @media (max-width: $md) {
        padding: 1.6rem 0;
    }
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        @media (max-width: $lg) {
            display: none;
        }
    }
    &__btn-close {
        @extend .btn-reset;
        margin-left: auto;
        padding: 1.2rem;
        color: $color-accent-1;
        border: 2px solid currentColor;
        border-radius: 1rem;
        transition: all .34s;
        &:hover {
            @media (min-width: $lg-mf) {
                background: $color-accent-1;
                color: #fff;
                border-color: $color-accent-1;
            }
        }
        .icon {
            width: 2rem;
            height: 2rem;
            display: block;
            @media (max-width: $lg) {
                width: 1.5rem;
                height: 1.5rem;
            }
        }
    }
    &__links {
        margin-left: auto;
        padding-right: var(--gap-fluid);
        display: flex;
        @media (max-width: $xll) {
            padding-right: 0;
        }
    }
    &__link {
        background: $color-white-1;
        padding-right: 3.2rem;
        padding-left: 3.2rem;
        & + & {
            margin-left: 1.2rem;
        }
        &:last-child {
            padding-right: 2.7rem;
            padding-left: 2.7rem;
        }
    }
    &__nav {
        margin-right: auto;
        align-self: flex-start;
        position: relative;
        &-dropdown {
            background: $color-white-1;
            min-width: 16.4rem;
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            font-weight: 800;
            font-size: 1.4rem;
            line-height: .98;
            list-style-type: none;
            color: $color-accent-3;
            border: 2px solid $color-accent-1;
            border-radius: 1rem;
            position: absolute;
            top: 0;
            left: 0;
            li {
                height: 4rem;
                margin: 0;
                padding: 0 1.3rem;
                display: none;
                align-items: center;
                &.is-active {
                    display: flex;
                    justify-content: space-between;
                    pointer-events: none;
                    order: -1;
                    &:after {
                        background: url(../images/icon/arrow-type-2.svg) 50% no-repeat;
                        background-size: contain;
                        width: 2.4rem;
                        height: 2.4rem;
                        margin-left: 1rem;
                        content: '';
                        display: block;
                        flex-shrink: 0;
                        transition: all .4s;
                    }
                }
            }
            a {
                display: block;
                &:hover {
                    text-decoration: none;
                }
            }
        }
        &.is-show {
            li {
                display: flex;
                &.is-active:after {
                    transform: rotate(-180deg);
                }
            }
        }
    }
}

.page-account--theme-dark {
    .header-account__logo {
        color: $color-white-1;
    }
}