#root {
    position: relative;
    overflow: clip;
}

.section {
    .fp-overflow {
        width: 100% !important;
    }
}

.container {
    width: 100%;
    max-width: 144rem;
    margin: 0 auto;
    padding: 0 var(--gap-fluid);
    overflow: hidden;
}

.container-fluid {
    width: 100%;
    margin: 0;
    padding: 0 var(--gap-fluid);
}

svg,
.u-icon {
    width: 1em;
    height: 1em;
    display: inline-block;
    color: inherit;
    fill: currentColor;
}

.img-cover {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.img-contain {
    display: block;
    position: relative;
    overflow: hidden;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: contain;
    }
}

.area-click {
    --indent: -.5rem;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: var(--indent);
        right: var(--indent);
        bottom: var(--indent);
        left: var(--indent);
    }
}

.link-cover {
    &:before {
        width: 100%;
        height: 100%;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
    }
}

.btn-reset {
    background: transparent;
    margin: 0;
    padding: 0;
    font-size: 0;
    outline: none;
    border: none;
    border-radius: 0;
}

.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
    width: 1px!important;
    height: 1px!important;
    margin: -1px!important;
    padding: 0!important;
    position: absolute!important;
    border: 0!important;
    clip: rect(0,0,0,0)!important;
    white-space: nowrap!important;
    overflow: hidden!important;
}

.font-family-accent {
    font-family: $ff-2;
}

.color-gray-1 {
    color: var(--color-gray-1);
}

.content {
    line-height: 1.4;
    > :first-child {
        margin-top: 0;
    }
    > :last-child {
        margin-bottom: 0;
    }
    p {
        margin: 0 0 1em;
    }
}

.btn {
    max-width: 100%;
    min-height: var(--btn-height);
    padding: var(--btn-padding-y) var(--btn-padding-x);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: 800;
    font-size: inherit;
    font-size: var(--btn-font-size);
    font-family: $ff-1;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
    border: none;
    border-radius: 0;
    outline: none;
    user-select: none;
    transition: all .24s;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    z-index: 0;
    &:hover {
        text-decoration: none;
    }
    &.disabled,
    &:disabled {
        background: #ccc;
        color: #000;
        opacity: .6;
        pointer-events: none;
    }
    &--theme {
        &-accent-outline {
            background: transparent;
            color: $color-accent-3;
            border: 2px solid $color-accent-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-3;
                    color: $color-white-1;
                }
            }
            &.is-active,
            &:focus,
            &:active {
                background: $color-gray-2;
                color: $color-accent-1;
            }
        }
        &-accent-outline-fill {
            background: $color-gray-2;
            color: $color-accent-1;
            border: 2px solid $color-accent-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-1;
                    color: $color-white-1;
                }
            }
            &.is-active,
            &:focus,
            &:active {
                background: $color-accent-1;
                color: $color-white-1;
            }
            &.disabled,
            &:disabled {
                background: $color-white-2;
                color: $color-white-3;
                border-color: $color-white-3;
                opacity: 1;
                pointer-events: none;
            }
        }
        &-accent-fill {
            background: $color-accent-1;
            color: $color-white-1;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-3;
                }
            }
            &.is-active,
            &:focus,
            &:active {
                background: $color-accent-3;
            }
        }
        &-white-outline {
            background: $color-accent-1;
            color: $color-white-2;
            border: 2px solid $color-white-2;
            border-radius: 1rem;
            &:hover {
                @media (min-width: $lg-mf) {
                    background: $color-accent-3;
                }
            }
            &.is-active,
            &:focus,
            &:active {
                background: $color-accent-3;
            }
        }
    }
    &--size {
        &-xs {
            --btn-height: 4.4rem;
            --btn-padding-x: 0;
            --btn-padding-y: 0;
            --btn-font-size: 0;
        }
        &-sm {
            --btn-height: 4.8rem;
            --btn-padding-x: 2.2rem;
            --btn-padding-y: 1.2rem;
            --btn-font-size: var(--fs-7);
            @media (max-width: $md) {
                --btn-padding-x: 1.2rem;
            }
        }
        &-md {
            --btn-height: 5.6rem;
            --btn-padding-x: 3rem;
            --btn-padding-y: 1.7rem;
            --btn-font-size: var(--fs-7);
        }
    }
    &--type {
        &-action {
            width: var(--btn-height);
            padding-right: 0 !important;
            padding-left: 0 !important;
        }
    }
    &--width {
        &-100 {
            width: 100%;
        }
    }
    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        z-index: 1;
    }
    &__title {
    }
    &__icon {
        width: 2.8rem;
        height: 2.8rem;
        margin-top: -1rem;
        margin-bottom: -1rem;
        flex-shrink: 0;
        @media (max-width: $lg) {
            width: 2.4rem;
            height: 2.4rem;
        }
        &--dir {
            &-r {
                margin-right: 1.2rem;
            }
        }
    }
}

.btn-simple {
    @extend .btn-reset, .area-click;
    width: 100%;
    padding: 1rem;
    display: block;
    font-weight: 500;
    font-size: var(--fs-default);
    color: $color-accent-1;
    text-align: center;
    transition: all .4s;
    cursor: pointer;
    &:hover {
        text-decoration: none;
        @media (min-width: $lg-mf) {
            opacity: .6;
        }
    }
}

.logo {
    @extend .area-click;
    display: inline-block;
    font-weight: normal;
    font-size: var(--fs-5);
    font-family: $ff-2;
    text-transform: uppercase;
    white-space: nowrap;
    transition: all .34s;
    transition-property: opacity;
    cursor: pointer;
    &:hover {
        text-decoration: none;
        @media (min-width: $lg-mf) {
            opacity: .6;
        }
    }
}

.u-input {
    width: 100%;
    min-height: var(--input-height);
    padding: var(--input-padding-y) var(--input-padding-x);
    display: block;
    font-weight: 500;
    font-size: var(--input-font-size);
    line-height: 1.35;
    border: none;
    border-radius: 0;
    outline: none;
    position: relative;
    transition: all .28s;
    &:hover {
        text-decoration: none;
    }
    &--icon {
        &-left {
            padding-left: 2.8rem;
            @media (max-width: $md) {
                padding-left: 2.6rem;
            }
        }
    }
    &--theme {
        &-dark-light {
            background: transparent;
            color: $color-accent-1;
            border: solid rgba($color-accent-1, .5);
            border-width: 0 0 2px;
            &:hover {
                @media (min-width: $lg-mf) {
                    border-color: $color-accent-2 !important;
                    box-shadow: 0 1.4rem 1.2rem -1.2rem rgba($color-accent-3, .3);
                }
            }
            &:focus {
                border-color: $color-accent-2 !important;
                box-shadow: 0 1.4rem 1.2rem -1.2rem rgba($color-accent-3, .6) !important;
            }
            &::placeholder {
                color: $color-gray-3;
            }
        }
        & + .icon {
            color: $color-gray-3;
        }
    }
    &--size {
        &-md {
            --input-height: 5rem;
            --input-padding-y: 1rem;
            --input-padding-x: 0;
            --input-font-size: var(--fs-7);
            font-weight: 500;
            @media (max-width: $md) {
                --input-height: 4.4rem;
                --input-padding-y: .8rem;
                --input-font-size: 1.4rem;
            }
        }
    }
    &[type="date"]::-webkit-inner-spin-button,
    &[type="date"]::-webkit-calendar-picker-indicator {
        display: none;
        -webkit-appearance: none;
        appearance: none;
    }
    &[type='date']::-webkit-calendar-picker-indicator {
        background: transparent;
        bottom: 0;
        color: transparent;
        cursor: pointer;
        height: auto;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        width: auto;
    }
}

.u-input-code {
    background: transparent;
    width: 100%;
    padding: 1.4rem 0;
    display: block;
    font-weight: normal;
    font-size: 10rem;
    line-height: 1.35;
    font-family: $ff-2;
    color: $color-accent-1;
    text-align: center;
    border: solid rgba($color-gray-3, .5);
    border-width: 0 0 2px;
    border-radius: 0;
    appearance: none;
    outline: none;
    position: relative;
    transition: all .28s;
    @media (max-width: $lg) {
        padding: 0 .6rem;
        font-size: 5rem;
    }
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        margin: 0;
        -webkit-appearance: none;
        appearance: none;
    }
    &[type=number] {
        -moz-appearance: textfield;
    }
    &:hover {
        text-decoration: none;
        @media (min-width: $lg-mf) {
            border-color: $color-accent-2 !important;
            box-shadow: 0 1.4rem 1.2rem -1.2rem rgba($color-accent-3, .3);
        }
    }
    &:focus {
        border-color: $color-accent-2 !important;
        box-shadow: 0 1.4rem 1.2rem -1.2rem rgba($color-accent-3, .6) !important;
    }
}

.l-input-area {
    &__label {
        margin: 0 0 .2rem;
        display: block;
        font-weight: 500;
        @media (max-width: $md) {
            font-size: 1.2rem;
        }
    }
    &__input-wrapper {
        display: flex;
        align-items: center;
    }
    &__input {
        flex-grow: 1;
        &:not(:focus).is-input-error {
            color: $color-red-1;
            border-color: rgba($color-red-1, .5);
        }
    }
    &__input-icon {
        width: 1.8rem;
        height: 1.8rem;
        margin: 0 -1.8rem 2px 0;
        flex-shrink: 0;
        color: $color-gray-3;
        transition: all .4s;
        pointer-events: none;
        order: -1;
        @media (max-width: $md) {
            width: 1.6rem;
            height: 1.6rem;
            margin-right: -1.6rem;
        }
    }
    &__input:not(:focus).is-input-error ~ &__input-icon {
        color: $color-red-1;
    }
    &__error {
        margin: .3rem 0 0;
        font-weight: 500;
        font-size: 1.2rem;
        color: $color-red-1;
        text-align: right;
        @media (max-width: $sm) {
            font-size: 1.1rem;
        }
    }
    &__input-code-wrapper {
        margin-top: -2.8rem;
        display: flex;
    }
    &__input-code {
        flex-grow: 1;
        &:not(:last-child) {
            margin-right: 2.4rem;
        }
    }
    &__appeal {
        margin: .3rem 0 0;
        font-weight: 500;
        font-size: 1.2rem;
        color: $color-red-1;
        text-align: right;
        @media (max-width: $sm) {
            font-size: 1.1rem;
        }
        a {
            box-shadow: 0 1px 0 rgba($color-red-1, .3);
            transition: all .4s;
            &:hover {
                text-decoration: none;
                @media (min-width: $lg-mf) {
                    box-shadow: 0 1px 0 rgba($color-red-1, 1);
                }
            }
        }
    }
}

.sc-block {
    &__list {
        margin: -.7rem;
        padding: 0;
        display: flex;
        list-style-type: none;
        @media (max-width: $sm) {
            margin: -.9rem;
        }
    }
    &__item {
        margin: .7rem;
        padding: 0;
        @media (max-width: $sm) {
            margin: .9rem;
        }
    }
    &__link {
        background: $color-white-1;
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $color-accent-1;
        border-radius: 50%;
        transition: all .4s;
        @media (max-width: $sm) {
            width: 4.8rem;
            height: 4.8rem;
        }
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
                background: $color-accent-1;
                color: $color-white-1;
            }
        }
    }
    &__icon {
        width: 2rem;
        height: 2rem;
        display: block;
    }
}

.services-card {
    background: $color-white-1;
    padding: 3rem;
    border-radius: 2rem;
    @media (max-width: $lg) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }
    &__inner {
    }
    &__counter {
        margin: -2.4rem 0 4.8rem;
        font-weight: normal;
        font-size: 16rem;
        line-height: 1;
        font-family: $ff-2;
        @media (min-width: $lg-mf) {
            margin-top: -2.2rem;
            margin-bottom: 2.4vh;
        }
        @media (max-height: 940px) {
            margin-bottom: 1.4rem;
            font-size: 14rem;
        }
        @media (max-width: $xl) {
            margin-top: -1rem;
            font-size: 12rem;
        }
        &:after {
            content: counter(services-card-counter);
            display: block;
            font: inherit;
        }
    }
    &__title {
        max-width: 50rem;
        min-height: 2.08em;
        margin: 0 0 2.2rem;
        line-height: 1.04;
        text-transform: uppercase;
        @media (max-width: $xl) {
            margin-bottom: 2rem;
            font-size: 2.8rem;
        }
        @media (max-width: $lg) {
            min-height: 0;
        }
    }
    &__description {
        margin: 0 2.6rem 0 0;
        font-weight: 500;
        font-size: var(--fs-5);
        color: rgba($color-accent-1, .7);
        @media (max-width: $xl) {
            font-size: 1.8rem;
        }
    }
}

.u-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
        min-height: 2.4rem;
        margin: 0;
        padding: 0 0 0 3.4rem;
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: var(--fs-5);
        line-height: 1.08;
        position: relative;
        @media (max-width: $md) {
            min-height: 2rem;
            padding-left: 2.6rem;
        }
        &:not(:last-child) {
            margin-bottom: 2rem;
            @media (max-width: $md) {
                margin-bottom: 1.8rem;
            }
        }
        &:before {
