.course-card {
    font-size: var(--fs-6);
    color: $color-black-2;
    @media (max-width: $md) {
        font-size: 1.4rem;
    }
    &__inner {
        padding: 1.8rem 2.4rem;
        border: 2px solid $color-accent-1;
        border-radius: 1rem;
        position: relative;
    }
    &__header {
        margin-bottom: 1.5rem;
        padding-bottom: 1.5rem;
        font-weight: 500;
        line-height: 1.04;
        border-bottom: 1px solid rgba($color-accent-1, .2);
    }
    &__body {
        display: flex;
    }
    &__picture {
        width: 10.2rem;
        height: 10.2rem;
        margin: 0 4rem -1.8rem 1rem;
        flex-shrink: 0;
        align-self: flex-end;
        @media (max-width: $md) {
            width: 5.8rem;
            height: 5.8rem;
            margin-right: 1.2rem;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: center bottom;
        }
    }
    &__content {
        padding-top: 1.2rem;
        @media (max-width: $md) {
            padding-top: 0;
        }
    }
    &__title {
        margin: 0 0 .8rem;
        font-weight: normal;
        font-family: $ff-2;
        color: $color-accent-1;
    }
    &__link {
        @extend .link-cover;
        transition: all .4s;
        &:hover {
            text-decoration: none;
            @media (min-width: $lg-mf) {
                opacity: .6;
            }
        }
    }
    &__status {
        display: flex;
        align-items: center;
        font-weight: 600;
        font-size: var(--fs-default);
        &:before {
            background: currentColor;
            width: 1.2rem;
            height: 1.2rem;
            margin-right: .7rem;
            content: '';
            display: block;
            flex-shrink: 0;
            border-radius: 50%;
        }
        &--hold {
            color: $color-yellow-1;
        }
        &--active {
            color: $color-green-1;
        }
        &--completed {
            color: $color-red-1;
        }
    }
}